#body-row {
  margin-left:0;
  margin-right:0;
}

.mainWrapper {
 
    float: left;
  height:93.4vh;
  margin-bottom: 0;
}

#sidebar-container {
  min-height: 93.4vh;   
  background-color: rgba(91, 153, 63, 0.849);
  padding: 0;
  margin-bottom: 0;
}

.hii {
    cursor: pointer;
}

/* Sidebar sizes when expanded and expanded */
.sidebar-expanded {
  width: 300px;
}
.sidebar-collapsed {
  width: 60px;
}

/* Menu item*/
#sidebar-container .list-group a {
  background-color: rgba(255, 253, 253, 0.466);
  height: 50px;
  color: rgb(34, 33, 33);
}

/* Submenu item*/
#sidebar-container .list-group .sidebar-submenu a {
  height: 45px;
  padding-left: 30px;
}
.sidebar-submenu {
  font-size: 0.9rem;
}
.hi {
  visibility: hidden;
}
/* Separators */
.sidebar-separator-title {
  background-color: rgb(255, 253, 253);
  height: 35px;
}
.sidebar-separator {
  background-color: rgb(255, 255, 255);
  height: 25px;
}
.logo-separator {
  background-color: rgb(255, 255, 255);    
  height: 60px;
}

/* Closed submenu icon */
#sidebar-container .list-group .list-group-item[aria-expanded="false"] .submenu-icon::after {
content: " \F0D7";
font-family: FontAwesome;
display: inline;
text-align: right;
padding-left: 10px;
}
/* Opened submenu icon */
#sidebar-container .list-group .list-group-item[aria-expanded="true"] .submenu-icon::after {
content: " \F0DA";
font-family: FontAwesome;
display: inline;
text-align: right;
padding-left: 10px;
}

.hiii {
  position: absolute;
  width: 300px;
  margin-top:400px;
}

@media only screen and (max-width: 768px) {
  .recentBill
  .list-group {
      display: none;
  }

  

  .hiii{
    
    visibility: visible;
    position: absolute;
  width: 100%;
  margin-top:0px;
  text-align: left;
  height: 100%;
  z-index: 1;
  
  }

  .show {
    background-color: white;
    z-index: 1;
    height: 100%;
  }

  .friends {
    display: none;
  }

  .fa-users {
    border: 1px solid black;
    padding: 10px;
    border-radius: 20px;
    background-color: white;
  }

  .toggleMe {

    border: 2px solid black;
    z-index: 1;
    padding: .25rem .75rem;
  font-size: 1.25rem;
  line-height: 1;
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: .25rem;
  }
}
.chat-main {
    position: absolute;
    
    left: 0;
    margin: 0px 50px 30px 0px;
    padding: 10px;
    max-height: 70%;
    
    height: 44.5%;
    width: 300px;
    margin-bottom: 0;
    margin-top: 0px;
}


.friendList {
    text-decoration: none;
    list-style: none;
    padding: 10px 0px;
    width: 250px;
    margin-left: -28px;
    cursor: pointer;
    transition: .3s ease-in-out;
}

.friendListOff {
    text-decoration: none;
    list-style: none;
    padding: 10px 0px;
    width: 250px;
    margin-left: -28px;
    cursor: default;
    transition: .3s ease-in-out;
}

.sendMsg {
    width: 300px;
    margin-left: -10px;
    background-color: rgba(121, 185, 112, 0.568);
    transition: .2s ease-in-out;
    margin-bottom: 0;
}

.sendMsg:hover {
    background-color: rgba(120, 185, 112, 0.267)
}

.msg {
    margin-left: 0px;
    margin-top: 0px;
    width: 300px;
    height: 100px;
    margin-left: -10px;
    text-align: left;
    margin-bottom: 0;
}

.friendList:hover{
    font-size: 1.2em;
}


.onlineHead {
    border: 2px groove rgba(226, 226, 226, 0.466);
    padding: 2px;
    background-color: rgba(255, 255, 255, 0.685);
    text-align: left;
}

.chatUser {
    float: left;
    margin-left: 60px;
    margin-top: -8px;
}

.back {
    cursor: pointer;
    float: left;
}

.messages {
    clear: both;
    
    height: 150px;
    margin-bottom: 0;

}

.message {

    list-style: none;
}

.chatApp {
    position: relative;
}

.onOff {
   visibility: visible;

}

.chat-main {
    visibility: hidden;
    margin-top: -160px;
}

@media only screen and (max-width: 768px) {
    .mainWrapper {
        display: none;
    }

    .chat-main{
        width: 100%;
     
    }

    .messages {
        width: 100%;
        overflow-x: scroll
    }

    .msg{
        margin-left: 0;
        width: 100%;
        
   
    }
    .chatty {
        width: 100%;
    }

    .sendMsg {
        
        width: 200px;
        
        background-color: rgba(121, 185, 112, 0.568);
        transition: .2s ease-in-out;
        
        margin: 0px auto;
        left: 0;
        right: 0;
       
    
    }


  }



